/** Media Queries **/
.formulario {
  width: 100%;
}

.campo {
  display: flex;
  margin-bottom: 2rem;
  align-items: center;
}
.campo label {
  flex: 0 0 10rem;
}
.campo input {
  flex: 1;
  border: none;
  padding: 1.25rem;
  border-radius: 1rem;
}
.campo input:disabled {
  background-color: #6d7158;
  color: #FFFFFF;
  cursor: not-allowed;
}

.boton {
  background-color: #0da6f3;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .boton {
    width: auto;
  }
}
.boton:hover {
  background-color: #0a85c3;
  cursor: pointer;
}

.boton-seccion {
  background-color: #0da6f3;
  color: #FFFFFF;
  padding: 1.5rem 2rem;
  transition: background-color 300ms ease;
  font-weight: 700;
  width: 100%;
  border: none;
  text-transform: uppercase;
}
.boton-seccion:hover {
  cursor: pointer;
  background-color: #054362;
}

.btn-paginacion {
  width: fit-content;
}

.acciones {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .acciones {
    display: flex;
    justify-content: space-between;
  }
}
.acciones p {
  font-size: 1.6rem;
  margin-bottom: 2.5rem;
}
.acciones a {
  color: #0da6f3;
  align-self: center;
}
.acciones .redireccionar {
  color: #FFFFFF;
  background-color: #0da6f3;
  padding: 1rem 2rem;
  border-radius: 1rem;
}

.alerta {
  padding: 1rem;
  width: fit-content;
  border-radius: 1rem;
  font-weight: 700;
  margin: 2rem auto;
}
.alerta.error {
  background-color: #cb0000;
}
.alerta.exito {
  background-color: #329f00;
}

@media (min-width: 768px) {
  .tabs {
    display: flex;
    justify-content: space-between;
  }
}
.tabs .actual {
  background-color: #FFFFFF;
  color: #0da6f3;
}

@media (min-width: 768px) {
  .paginacion {
    display: flex;
    justify-content: space-between;
  }
}
.paginacion button {
  margin: 0 0 1rem 0;
  border: none;
  border-radius: 1rem;
}
.paginacion .ocultar {
  visibility: hidden;
}

.servicios .text-center {
  text-align: center;
}

.seccion h2 {
  margin-top: 5rem;
}

@media (min-width: 768px) {
  .listado-servicios {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.listado-servicios .servicios {
  background-color: #FFFFFF;
  transition: all 300ms ease;
  border-radius: 1rem;
  text-align: center;
  font-weight: 700;
}
.listado-servicios .servicios.seleccionado {
  background-color: #0da6f3;
}
.listado-servicios .servicios.seleccionado p {
  color: #FFFFFF;
}
.listado-servicios .servicios:hover {
  cursor: pointer;
  transform: scale(1.1);
}
.listado-servicios .servicios p {
  color: #1a1b15;
}
.listado-servicios .servicios .precio-servicio {
  color: #0da6f3;
}